html {
  scroll-behavior: smooth;
}
body {
  background-color: lightgrey;
  color: black;
  text-align: center;
}
a {
  /* background-color: lightseagreen; */
  text-decoration: none;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 10%;
}
a:link {
  color: black;
}
a:visited {
  color: black;
}
a:hover {
  color: blue;
  transition: all 2500ms ease;
}
#navbar {
  /* position: sticky; */
  /* grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; */
  /* cursor: pointer; */
  margin: 0;
  display: flex;
  width: 100%;
  position: fixed;
  padding: 0px;
  grid-gap: 10px;
  justify-content: space-between;
  color: black;
  background-color: lightblue;
}
#navbar div {
  padding: 20px;
  font-size: 26px;
  font-weight: bold;
}
.nongrid:hover {
  background-color: blue;
  color: black;
}
#navbar div:nth-child(.nongrid:hover) {
  background-color: blue;
  color: black;
}
#navbar div:hover {
  background-color: lightseagreen;
  border-radius: 5%;
  color: black;
}

/* Batasan Navbar Website  */

#mainpage {
  background-color: white;
  border-radius: 20px;
  width: 100%;
  height: 500px;
  padding-top: 25px;
  display: grid;
  grid-template-columns: 50% 50%;
  /* grid-template-rows: 25% 50% 25%; */
  grid-gap: 10px;
  align-items: center;
  align-content: center;
  justify-items: center;
}
.titleName {
  font-size: 40px;
  font-weight: bold;
  color: maroon;
}
.titleSub {
  font-size: 20px;
  font-style: italic;
  color: darkgrey;
}
.titleDesc {
  font-size: 15px;
  font-style: normal;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: lighter;
}
.profile {
  border-radius: 20%;
  box-shadow: 3px 0.5px black;
}

/* Batasan mainpage website */
#proSumm {
  background-color: white;
  border-radius: 20px;
  margin-top: 25px;
  width: 100%;
  height: 300px;
  /* padding-top: 25px; */
  display: grid;
  /* grid-template-rows: 1fr 2fr; */
  grid-template-columns: 1fr 4fr 1fr;
  grid-gap: 10px;
  align-items: center;
  align-content: center;
  justify-items: center;
}

#education {
  background-color: white;
  border-radius: 20px;
  /* margin-top: 30px; */
  padding-top: 50px;
  margin-bottom: 20px;
  width: 100%;
  height: 600px;
  display: grid;
  grid-template-rows: 2fr 1fr 2fr;
  grid-template-columns: 0.2fr 1fr 1fr 1fr 0.2fr;
  /* grid-gap: 100px; */
  column-gap: 100px;
  align-items: center;
  align-content: center;
  justify-items: center;
}
.proSummDetails {
  margin-top: -25px;
  font-size: 20px;
  text-align: justify;
  font-weight: lighter;
}
.edutitle {
  font-size: 25px;
  text-align: center;
  font-weight: bold;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.eduTop {
  /* width: 400px; */
  font-size: 30px;
  text-align: center;
  font-weight: bolder;
}
.eduText {
  font-size: 20px;
  text-align: justify;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.eduBottom {
  margin-bottom: 100px;
  font-size: 20px;
  font-family: "Segoe UI";
  text-decoration: underline;
}

/* Batasan education website */

#workExp {
  background-color: white;
  width: 100%;
  border-radius: 20px;
  margin-top: 25px;
  margin-bottom: 20px;
  height: 1200px;
  display: grid;
  grid-template-columns: 3% 52% 40% 3%;
  grid-gap: 10px;
}
.workDetails {
  padding-top: 50px;
  font-size: 20px;
  align-items: left;
  text-align: left;
  align-content: center;
  justify-items: center;
}

#manufacture {
  width: 100%;
  margin-top: 25px;
  margin-bottom: 25px;
  height: 800px;
  display: grid;
  grid-template-rows: 2fr 0.2fr 0.2fr 2fr;
  grid-gap: 15px;
}
.manufactureImg {
  width: 500px;
  height: 500px;
  border-radius: 20%;
  box-shadow: 3px 5px black;
  margin-top: 50px;
}
.manufactureImgTitle {
  padding-top: 50px;
  font-size: 25px;
  font-weight: bold;
  font-family: sans-serif;
}
.manufactureAbility {
  font-size: 20px;
  font-weight: 100;
  font-family: sans-serif;
}
#machineList {
  width: 100%;
  margin-top: 25px;
  margin-bottom: 25px;
  /* height: 1000px; */
  display: grid;
  grid-template-columns: 1fr 4fr 6fr;
  grid-gap: 25px;
}
#machineList li {
  /* padding-left: 100px; */
  font-size: 20px;
  font-family: sans-serif;
  text-align: left;
}

